*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.nav-list{
    height: 10vh;
    display: flex;
    width: 50%;
    justify-content: space-around;
    align-items: center;
    margin-left: auto;
    margin-top: 0;
    margin-bottom: 0;
    list-style: none;
    flex-direction: row-reverse;
}

.nav-links{
    text-decoration: none;
    font-size: 18px;
    color: black;
}


.home , .about , .contact {
    background-image: linear-gradient(to top, #5ee7df 0%, #b490ca 100%);
    height: 90vh;
    position: absolute;
    width: 100%;
}

.home-content,.about-content , .contact-content{
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    img{
        height: 70%;
    }
            
    h1{
        font-size: 50px;
        margin: 30px 0 10px;
    }
    p{
        font-size: 22px;
    }
}


   


        
